'use strict';

// Hamburger

var fullScreenInfoRow = document.querySelector('.main-content');
var fullScreenInfo = document.querySelector('.header__menu-nav');
var burgerMenu = document.querySelector('.header__burger-btn');
var iconMenu = document.querySelector('.header__line');
if (burgerMenu) {
   burgerMenu.addEventListener("click", function (e) {
      document.body.classList.toggle('js-lock');
      iconMenu.classList.toggle('js-is-active');
      fullScreenInfoRow.classList.toggle('js-is-active');
      fullScreenInfo.classList.toggle('js-is-active');
   });
}